import { Button, Container, Flex, Text } from '@chakra-ui/react';
import React from 'react';
import { Link } from 'react-router-dom';

export default function Header() {
  return (
    <Container maxW="5xl" p={4} position="fixed" top={0} left={0} right={0}>
      <Flex justifyContent="space-between" alignItems="center">
        <Link to="/">
          <Text fontSize={28} fontWeight="bold" py={2} px={4} color="white">
            Rentalase
          </Text>
        </Link>
        <Button bg="white" size="sm" px={6} color="#2835BD">
          Login
        </Button>
      </Flex>
    </Container>
  );
}
