import { Box, Button, Center, Container, Image, Text } from '@chakra-ui/react';
import React from 'react';
import Lottie from 'react-lottie';
import UnderConstruction from '../assets/images/UnderConstruction.json';
import Volunteering from '../assets/images/Volunteering.svg';
import Header from '../components/Header';

export default function Home() {
  return (
    <React.Fragment>
      <Header />
      <Box
        bgGradient="linear(45deg, #404EED, #2835BD)"
        position="relative"
        zIndex={-1}
        overflow="hidden"
      >
        <Image
          src={Volunteering}
          width={650}
          position="absolute"
          left="50%"
          top="50%"
          transform="translate(-50%, -50%)"
          opacity={0.2}
        />
        <Container
          maxW="5xl"
          minH="650px"
          display="flex"
          justifyContent="center"
          flexDir="column"
        >
          <Center flexDirection="column">
            <Text fontSize={48} display="inline" align="center" color="white">
              Welcome to{' '}
              <Text
                fontSize={48}
                bgClip="text"
                fontWeight="bold"
                display="inline"
                color="white"
              >
                Rentalase
              </Text>
            </Text>

            <Text mb={4} align="center" color="white">
              Now, you can easily to find product can use it, without you must
              have.
              <br />
              Register for to explore experience Rentalase.
            </Text>
            <Button px={6} color="#2835BD">
              Get Started
            </Button>
          </Center>
        </Container>
      </Box>
      <Container maxW="5xl" minHeight={200} my={8}>
        <Text
          align="center"
          fontSize={32}
          background="linear-gradient(60deg, #2835BD, #15239D)"
          bgClip="text"
          fontWeight="bold"
        >
          Soory, this page is under construction.
        </Text>
        <Lottie
          options={{ animationData: UnderConstruction }}
          width="100%"
          style={{ maxWidth: '900px' }}
        />
      </Container>
    </React.Fragment>
  );
}
